.app-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: rgb(19, 18, 18);
    min-height: 100vh;
}

.login-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px;
    width: 40%;
    border: 7px solid rgb(245, 144, 29);
    box-shadow: 0px 0px 30px rgb(192, 181, 174);
    border-radius: 12px;
}

@media screen and (max-width: 767px) {
    .login-container {
        width: 75%;
    }
}


.login-head {
    color: chartreuse;
    font-family: "Bree Serif";
    font-size: 60px;
    font-weight: bold;
}

@media screen and (max-width: 767px) {
    .login-head {
        font-size: 40px;
    }
}
 
.input-field-container {
    display: flex;
    flex-direction: column;
    width: 65%;
    margin-bottom: 10px;
}

.label {
    color: chartreuse;
    font-family: "Roboto";
    font-size: 12px;
    font-weight: bold;
}

@media screen and (max-width: 767px) {
    .label {
        font-size: 8px;
    }
}

.input-field {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 36px;
  padding-left: 20px;
  color: black;
  font-weight: bold;
  border: 3px solid rgb(192, 181, 174);
  border-radius: 6px;
  outline-color:chartreuse;
}

@media screen and (max-width: 767px) {
    .input-field {
        height: 26px;
    }
}

.error-msg {
    color: red;
    font-size: 16px;
    font-weight: bold;
    font-family: "Bree Serif";
}

.login-btn {
    background-color: rgb(19, 18, 18);
    border: 1px solid chartreuse;
    color: #ffffff;
    padding-left: 18px;
    padding-right: 18px;
    padding-top: 5px;
    padding-bottom: 5px;
    border-radius: 6px;
    font-weight: bold;
    cursor: pointer;
    margin-top: 6px;
}

@media screen and (max-width: 767px) {
    .login-btn {
        font-weight: 400;
        padding-left: 10px;
        padding-right: 10px;
        padding-top: 4px;
        padding-bottom: 4px;
        font-size: 10px;
    }

    .error-msg {
        font-size: 10px;
    }
}